@import '~modularscale-sass/stylesheets/modularscale';

$hair-space      : 0.0417em; // 0.7px || Width varies, but narrower than thin space
$fine-space      : 0.125em; // 2px || Bien ! For French punctuation
$one-sixth-space : 0.1667em; // 2.7px || Might be suitable for grouping
$thin-space      : 0.2em; // 3.2px || Recommended minimum width of space
$math-space      : 0.2222em; // 3.6px ||a + b Around operators (according to Chicago)
$default-space   : 0.25em; // 4px || Average width of normal space
$thick-space     : 0.2778em; // 4.4.px || a = b Around relational operators (according to Chicago)
$one-third-space : 0.3333em; // 5.3px || Width of space in a wide font
$en-space        : 0.5em; // 8px || Recommended maximum width of space, width of en-dash
$em-space        : 1em; // 16px || width of em-dash

$emoji                  : 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';

$sans-serif-fallback    : system-ui, -apple-system, BlinkMacSystemFont, 'Avenir Next', Avenir,  'Segoe UI', 'Helvetica Neue', Helvetica, 'Noto Sans', 'Liberation Sans', Ubuntu, Roboto, Noto, Arial, sans-serif, $emoji;

$serif-fallback         : 'Palatino Linotype', palatino, palladio, 'URW Palladio L', 'Book Antiqua', baskerville, 'Bookman Old Style', 'Bitstream Charter', 'Nimbus Roman No9 L', garamond, 'Apple Garamond', 'ITC Garamond Narrow', 'New Century Schoolbook', 'Century Schoolbook', 'Century Schoolbook L', georgia, serif, $emoji;

$monospace-fallback     : ui-monospace, SFMono-Regular, Menlo, Monaco, 'Cascadia Mono', 'Segoe UI Mono', 'Roboto Mono', 'Oxygen Mono', 'Ubuntu Monospace', 'Source Code Pro', 'Fira Mono', 'Droid Sans Mono', 'Courier New', monospace, $emoji;

$font-family-sans-serif : $sans-serif-fallback;
$font-family-serif      : $serif-fallback;
$font-family-monospace  : 'FiraCode Nerd Font Mono', $monospace-fallback;
$font-family-code       : $font-family-monospace;
$font-family-base       : $font-family-sans-serif;

$yellow: #FFAB41;
$red: #800000;
$blue: #0A7AC2;

$gray-100: #DDDDDD;
$gray-200: #c5c5c5;
$gray-300: #adadad;
$gray-400: #969696;
$gray-500: #74747B;
$gray-600: #515155;
$gray-700: #303030;
$gray-800: #2D2D2D;
$gray-900: #161616;

$primary: $red;
$secondary: $gray-700;

$spacer: 1rem;

$spacers: (
	0: 0,
	1: $spacer * .25,
	2: $spacer * .5,
	3: $spacer * .75,
	4: $spacer,
	5: $spacer * 1.25,
	6: $spacer * 1.5,
	7: $spacer * 1.75,
	8: $spacer * 2,
	9: $spacer * 2.25,
	10: $spacer * 2.5,
	11: $spacer * 2.75,
	12: $spacer * 3,
);

$modularscale: (
  base: rem(18px),
  ratio: 1.125
);

$font-size-base:              ms(0); // Assumes the browser default, typically `16px`
$font-size-sm:                ms(-1);
$font-size-lg:                ms(1);

$h1-font-size:                ms(6);
$h2-font-size:                ms(5);
$h3-font-size:                ms(4);
$h4-font-size:                ms(3);
$h5-font-size:                ms(2);
$h6-font-size:                ms(1);

$display-font-sizes: (
  1: ms(12),
  2: ms(11),
  3: ms(10),
  4: ms(9),
  5: ms(8),
  6: ms(7)
);

$line-height-base:            1.5 !default;
$line-height-sm:              1.25 !default;
$line-height-lg:              2 !default;

$paragraph-margin-bottom: 1em;

$blockquote-margin-y: $paragraph-margin-bottom;

$headings-margin-bottom: $paragraph-margin-bottom * .5;

@import '~bootstrap/scss/variables';

$gray-colors: ();


@each $level, $value in $grays {
	$new: (
		'gray-#{$level}': $value,
	);
	$gray-colors: map-set($gray-colors, 'gray-#{$level}', $value);
}

$colors: map-merge($gray-colors, $colors);

$theme-colors: map-merge($colors, $theme-colors);

$theme-colors: map-set($theme-colors, 'muted', $text-muted);
$theme-colors: map-set($theme-colors, 'border-color', $border-color);

$container-widths: (
	sm: 100vw,
	md: 90vw,
	lg: 85vw,
	xl: 75vw,
	xxl: 1050px
);
